<template>
  <div>
    <el-row>
      <el-col :span="5">
        <div style="height: 700px; border-right: #999999 solid 1px">
          <div align="center" style="margin-top: 10%; font-size: x-large; font-weight: bold">
            
          </div>
          <div class="problem-level info">
            {{ level }}
          </div>
          <hr class="hr-line">
          <div class="info">
            {{ problemid }}
          </div>
          <div class="info">
            {{ problem_info }}
          </div>
          <div align="center" style="margin: 80px; color: #0079fe">
            <!-- <div @click="feedback"></div> -->
            <div @click="readblog"></div>
          </div>
        </div>
      </el-col>
      <el-col :span="14">
        <mid-part></mid-part>
      </el-col>
      <el-col :span="5">
        <div class="right-part">
          <div align="center" class="right-title">
            
          </div>
          <div class="subtitle">
            {{ solved }}
          </div>
          <hr class="hr-line">
          <el-table
              :data="solved_list"
              stripe
              class="hr-line">
            <el-table-column
                prop="rank"
                label="">
            </el-table-column>
            <el-table-column
                prop="accountNickname"
                label="">
            </el-table-column>
            <el-table-column
                prop="solveCostTime"
                label="">
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MidPart from "@/components/question/mid-part";

export default {
  name: 'question',
  components: {MidPart},
  data() {
    return {
      level: 4,
      problem_info: 'here is problem information',
      solved: 150,
      problemid: '',
      solved_list: [
        {
          accountNickname: '1',
          rank: 1,
          solveCostTime: '16: 11'
        }
      ]
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    readblog() {
      localStorage.setItem("blogid", "0000");
      this.$router.push("blog");
    },
    init() {
      this.problemid = localStorage.getItem('problemid')
      this.getinfo()
      this.getsolvelist()
    },
    async getinfo() {
      this.$axios.post("/problem/message", {
        problemId: this.problemid
      })
          .then((res) => {
            // alert("");
            // console.log(res.data);

            if (res.data.code === 0) {
              this.level = res.data.data.problemInfo.problemLevel
              this.problem_info = res.data.data.problemInfo.problemInfo
              console.log(this.problemid)
            }
            // alert(res.data.msg);
          })
    },
    async getsolvelist() {
      this.$axios.post("/solve/rank", {
        problemId: this.problemid
      })
          .then((res) => {
            // alert("");
            // console.log(res.data);

            if (res.data.code === 0) {
              this.solved = res.data.data.solveCount
              this.solved_list = res.data.data.solveRankInfoList
              console.log(this.solved_list)
            }
            // alert(res.data.msg);
          })
    },
    freshsolvelist:function (){
      this.getsolvelist()
      setTimeout(function () {
        this.freshsolvelist()
      }, 1000)
    }
  }
};
</script>

<style>

.hr-line {
  margin-left: 10%;
  width: 80%;
}

.subtitle {
  color: #999999;
  margin-left: 10%;
}

.right-title {
  margin-top: 10%;
  font-size: x-large;
  font-weight: bold;
}

.right-part {
  margin-top: 10%;
  border-left: #999999 solid 1px;
  height: 700px;
}

.mid {
  margin-top: 5%;
}

.info {
  margin-left: 10%;
}

.problem-level {
  color: #999999;
}

.bg-purple {
  background-color: blueviolet;
}

</style>